body {
  margin: 0;
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Righteous';
  src: local('Righteous'), url(./assets/fonts/Righteous-Regular.ttf) format('truetype');
}


@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'), url(./assets/fonts/Montserrat-VariableFont_wght.ttf) format('truetype');
}

.oscillateVertically {
  animation: moveDownVeryLittle 3s ease-in-out alternate-reverse infinite;
}
.rotate {
  transform: rotateZ(360deg);
  animation: spin 2s  ease-in-out both infinite;
}

.twoColumnContainer {
  display: flex;
  align-items:flex-start
}


@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 900px) {
  .twoColumnContainer {
    /* it place the items in vertical direction */
    flex-direction: column;
    align-items:center;
  }

  .invisibleOnMobile {
    display: none;
  }

  .box {
    margin: 0 0 1rem;
  }

  .wfullm {
    width: 100%;
  }
  .mainLogo {
    margin-top: 0.5rem;
  }
}

@media (orientation: landscape) {
  .heightSmallerOnPortrait {
    margin-top: 14rem;
    margin-left: -6rem;
  }
  .mainLogo {
    justify-self: start;
    margin-left: 1.5rem;
    margin-top: 1.5rem;
  }
  .textBubble {
    background-image: url("./assets/images/background/text_bubble_small.svg");
    margin-right: 0rem;
    padding-bottom: 6rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: 1.5rem;
    height: 18rem;
    width: 19rem;
  }
  .bigTextBubble {
    background-image: url('./assets/images/background/text_bubble_big.svg');
    margin-bottom: -4rem;
    padding-left: 2.5rem;
    padding-right: 2.5rem;
    padding-bottom: 4.5rem;
    padding-top: 1.5rem;
    height: 36rem;
    width: 38rem;
  }
  .rotatingStar {
    margin-right: 10rem;
    margin-top: 5rem;
  }
}

@media (orientation: portrait) {
  .heightSmallerOnPortrait {
    margin-top: 1rem;
  }
  .mainLogo {
    justify-self: center;
  }
  .twoColumnContainer {
    /* it place the items in vertical direction */
    flex-direction: column;
    align-items:center;
  }

  .textBubble {
    background-image: url('./assets/images/background/text_bubble_small_down.svg');
    margin-bottom: -4rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-bottom: 2.5rem;
    padding-top: 1.5rem;
    height: 14rem;
    width: 19rem;
  }

  .bigTextBubble {
    background-image: url('./assets/images/background/text_bubble_small_down.svg');
    margin-bottom: -4rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-bottom: 2.5rem;
    padding-top: 1.5rem;
    height: 14rem;
    width: 19rem;
  }

  .rotatingStar {
    margin-right: 2rem;
    margin-top: 4rem;
  }
}

@keyframes moveDownVeryLittle {
  from {
    transform: translateY(-0.08rem);
  }
  to {
    transform: translateY(0.08rem);
  }
}
